.analyte-desc {
    margin: 1px 5px;
}

.t6-title {
    width: 100%;
    font-weight: bold;
    margin: 14px 0px;
    color: blue;
    font-size: 15px;
}

.imgpart img {
    width: 100%;
}

.imgpart {
    margin-bottom: 2rem;
}

.imgpart p {
    text-align: left;
    padding-bottom: 0.5rem;
    margin: 0;
    font-size: 13px;
}

.contentpart {
    margin-bottom: 2rem;
}

.sigpart {
    margin-bottom: 1rem;
}

.contP,
.SignP {
    margin-bottom: 2rem;
}

.contentpart .analyte-desc,
.sigpart .analyte-desc {
    font-size: 16px;
}

.disclaimer {
    padding: 10px 39px 8.5px 10px;
    border-radius: 5px;
    background-color: #f4f4f4;
}

.note-title {
    background: #0072ce;
    padding: 14px;
    font-size: 14px;
    font-weight: bold;
    color: #fff;
    border-radius: 7px 7px 0px 0px;
}

.contentpart .analyte-desc {
    margin: 0;
}

.contentpart p {
    margin: 0;
}

.Headcontent .contP {
    margin-bottom: 0 !important;
}

.sigpart p {
    margin: 0;
}

.mat-form-field {
    width: auto;
}

.capitalize-first-letter {
    text-transform: lowercase;
    /* Convert all text to lowercase */
}

.capitalize-first-letter::first-letter {
    text-transform: uppercase !important;
    /* Capitalize the first letter */
}

.temp14 tr td:nth-child(1) {
    width: 220px;
}

.tbl-border {
    border: 1px solid #343a40;
    border-top: 0px !important;
    border-radius: 5px;
}